@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap);
.shorten{display:grid;grid-template-rows:1fr auto 1fr;grid-template-areas:"instructions" "form" "short";height:100%}.shorten p{font-size:0.8rem;text-align:center;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content}.shorten p.instructions{place-self:end center}.shorten p.short{place-self:start center}.shorten form{width:100%;display:flex;flex-direction:row;margin:1rem 0;flex-wrap:wrap;justify-content:center}.shorten form .input-group{position:relative;flex-grow:1}.shorten form .input-group input{box-sizing:border-box;width:100%;min-width:19.5rem;height:3rem;outline:none;font-size:unset;margin-bottom:1rem}.shorten form .input-group label{position:absolute;top:-0.5rem;left:0.6rem;padding:0 0.2rem;margin-bottom:1rem;z-index:1;background-color:#27292e}.shorten form button{height:3rem;background:linear-gradient(145deg, #ff4c9c 0%, #c050d8 40%, #1463ff 100%);border-radius:15px;font-size:0.8rem;color:inherit;border:none;padding:0.5rem 1rem;cursor:pointer;outline:inherit;margin-left:0.5rem;letter-spacing:6px}.shorten form button:hover{background:linear-gradient(145deg, #e6458d 0%, #a947bf 40%, #1259e6 100%)}.shorten form .gradient-border{background:linear-gradient(#27292e, #27292e) padding-box,linear-gradient(145deg, #ff4c9c 0%, #c050d8 40%, #1463ff 100%) border-box;color:white;padding:0.75rem;border:4px solid transparent;border-radius:15px}.shorten .short a{color:#ff4c9c}.shorten .error{color:#ff4c9c}

.redirect{display:flex;align-items:center;justify-content:center;height:100%}.redirect .error{width:100%;height:100%;display:grid;grid-template-areas:"error" "message" "back-to-home";text-align:center}.redirect .error h1{grid-area:error;margin-top:0;color:#ff4c9c}.redirect .error .message{grid-area:message;display:inline-block}.redirect .error .back-to-home{grid-area:back-to-home;color:#ff4c9c}

html{font-size:32px}body{margin:0;height:100vh;width:100vw;background-color:#27292e;color:white}#root{height:100%;font-family:'Montserrat', sans-serif}.app{display:grid;grid-template-columns:10% auto 10%;grid-template-rows:10% auto 10%;grid-template-areas:". . ." ". main ." "footer footer footer";height:100%}main{grid-area:main}footer{grid-area:footer;display:flex;align-items:flex-end;justify-content:flex-end}footer a{padding:0 0.2rem}footer a img{height:1rem;width:1rem;padding:0.05rem;cursor:pointer;border:2px solid transparent;border-radius:2rem}footer a img:hover{border:2px solid white}

